@use "../../scss/breakpoints";

.zoom-button {
  width: 76px;
  height: 76px;
  font-weight: 400;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: 0;
  background-color: var(--back-primary);
  border: none;
  border-radius: 50%;
  box-shadow: 0 4px 20px 0 rgb(0 0 0 / 25%);
  cursor: pointer;

  &:hover {
    background-color: var(--gray-200);
  }
}
