:root {
  --color-black: #000;
  --color-white: #fff;
  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-base: var(--font-sans);
  --font-sans: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --front-primary: #333333;
  --front-primary-inverted: #ffffff;
  --back-primary: #ffffff;
  --gray-200: #f3f3f3;
  --gray-300: #a7a7a7;
  --gray-400: #9b9b9b;
  --gray-500: #d9d9d9;
  --gray-600: #cbcbcb;
  --blue: #74dcf3;
  --blue-dark: #03a9f4;
  --green: #48d055;
  --aside-width: 260px;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden],
.hidden {
  display: none !important;
}

@font-face {
  font-weight: 300;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Roboto-Light.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Roboto-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("{{fonts}}Roboto-Medium.woff2") format("woff2");
}
.aside__link {
  display: flex;
  gap: 17px;
  align-items: center;
  padding: 0 24px;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0;
  text-decoration: none;
  color: var(--front-primary);
}
.aside__link:hover {
  color: var(--gray-500);
}
.aside__link-icon {
  width: 18px;
  height: 18px;
  color: var(--front-primary);
  transition: color 0.2s ease-in;
}

.button {
  padding: 10px 50px;
  color: var(--front-primary-inverted);
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0;
  background-color: var(--blue-dark);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in;
}
.button:disabled {
  background-color: var(--gray-300);
  pointer-events: none;
}
.button:hover {
  background-color: var(--blue);
}

.header {
  display: flex;
  gap: 16px;
  align-items: center;
  height: 56px;
}
.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--aside-width);
  height: 56px;
  margin: 0;
  color: var(--front-primary-inverted);
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
  background-color: var(--blue-dark);
}
.header__user-link {
  margin-right: 67px;
  margin-left: auto;
}

@keyframes loader {
  0% {
    transform: scale(0, 0);
    opacity: 0.8;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
.loader {
  display: inline-block;
  width: 100px;
  height: 100px;
  padding: 0;
  text-align: left;
}
.loader span {
  position: absolute;
  display: inline-block;
  width: 100px;
  height: 100px;
  background: var(--blue);
  border-radius: 100%;
  animation: loader 1.5s linear infinite;
}
.loader span:last-child {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.menu-button {
  padding: 0;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
.menu-button__icon {
  width: 18px;
  height: 18px;
  color: var(--front-primary);
  transition: color 0.2s ease-in;
}
.menu-button__icon:hover {
  color: var(--gray-500);
}

.page {
  display: grid;
  grid-template-areas: "header header" "sidebar center";
  grid-template-columns: var(--aside-width) 1fr;
  gap: 20px 30px;
}
.page__title {
  margin: 0;
  font-weight: 300;
  font-size: 28px;
  line-height: 33px;
  letter-spacing: 0;
}
.page__title + .page__content {
  margin-top: 28px;
}
.page__header {
  grid-area: header;
}
.page__main {
  margin-top: 11px;
  margin-right: 30px;
}

@keyframes loading {
  to {
    transform: rotate(1turn);
  }
}
.seats {
  position: relative;
  min-width: 100px;
  height: 100%;
  min-height: 350px;
  max-height: calc(100vh - 200px);
  overflow: auto;
  background: rgb(249, 249, 249);
  border: 1px solid rgb(155, 155, 155);
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.seats ::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}
.seats__inner {
  position: relative;
  display: grid;
  grid-template-areas: "sectorThree sectorFour" "arena arena" "sectorOne sectorTwo";
  grid-template-columns: 1fr 1fr;
  gap: 46px;
  justify-items: center;
  padding: 90px 240px 50px;
}
.seats__inner.loading {
  opacity: 0.1;
}
.seats__inner.draggable {
  cursor: grab;
}
.seats__loader {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}
.seats__filters {
  position: absolute;
  top: 37px;
  left: 39px;
  z-index: 2;
  display: flex;
  gap: 40px;
}
.seats__controls {
  position: absolute;
  top: 50%;
  right: 75px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 23px;
  transform: translateY(-50%);
}
.seats__sector--one {
  grid-area: sectorOne;
}
.seats__sector--two {
  grid-area: sectorTwo;
}
.seats__sector--three {
  grid-area: sectorThree;
}
.seats__sector--four {
  grid-area: sectorFour;
}
.seats__arena {
  display: flex;
  grid-area: arena;
  align-items: center;
  justify-content: center;
  width: 1037px;
  height: 528px;
  border: 4px solid var(--blue-dark);
  border-radius: 100px;
}
.seats__arena-inner {
  width: 962px;
  height: 462px;
  border: 2px solid var(--blue-dark);
  border-radius: 75px;
}
.seats__result {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: flex-end;
  height: 70px;
  padding: 16px 19px;
  border: 1px solid var(--gray-400);
  border-top-width: 0;
}
.seats__result-text {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
  white-space: nowrap;
}
.seats__result-cards {
  display: flex;
  flex-direction: row-reverse;
  gap: 5px;
  margin-right: 20px;
}

.sector {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sector__title {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
}
.sector__row-svg {
  width: 170px;
  height: 20px;
  overflow: visible;
}
.sector__row {
  display: flex;
  gap: 30px;
  align-items: center;
}
.sector__title + .sector__row {
  margin-top: 25px;
}
.sector__row + .sector__row {
  margin-top: 20px;
}
.sector__row-places {
  width: 197px;
  height: 20px;
}
.sector__row-name {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0;
}
.sector__place-hole {
  r: 0;
  pointer-events: none;
}
.sector__place {
  position: relative;
  display: inline-block;
  width: 10px;
  min-width: 10px;
  height: 10px;
  margin: 0 3px;
  color: var(--gray-500);
  border-radius: 50%;
  pointer-events: none;
}
.sector__place.available {
  cursor: pointer;
  pointer-events: all;
}
.sector__place[data-color=green] {
  color: var(--green);
  r: 10;
}
.sector__place[data-color=blue] {
  color: var(--blue);
  r: 10;
}
.sector__place.added {
  r: 12;
}
.sector__place.added + .sector__place-hole {
  r: 3;
}

.user-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 34px;
  height: 34px;
  color: var(--blue);
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0;
  text-decoration: none;
  background-color: var(--gray-200);
  border-radius: 50%;
  transition: background-color 0.2s ease-in;
}
.user-link:hover {
  background-color: var(--gray-400);
}

.zoom-button {
  width: 76px;
  height: 76px;
  font-weight: 400;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: 0;
  background-color: var(--back-primary);
  border: none;
  border-radius: 50%;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.zoom-button:hover {
  background-color: var(--gray-200);
}

.tooltip {
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 13px;
  align-items: center;
  padding: 10px 30px 35px;
  background-color: var(--back-primary);
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.25);
  opacity: 0;
  transition: opacity 0.2s ease-in;
}
.tooltip::after {
  position: absolute;
  bottom: -10px;
  width: 20px;
  height: 20px;
  background-color: var(--back-primary);
  transform: rotate(45deg);
  content: "";
}
.tooltip--bottom::after {
  position: absolute;
  top: -10px;
  bottom: auto;
  width: 20px;
  height: 20px;
  background-color: var(--back-primary);
  transform: rotate(45deg);
  content: "";
}
.tooltip__price {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
}
.tooltip__place {
  display: flex;
  flex-direction: column;
  color: var(--gray-300);
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
  text-align: center;
}

.filter-button {
  display: flex;
  gap: 16px;
  align-items: center;
  padding: 9px 29px 10px 13px;
  background-color: var(--back-primary);
  border: none;
  border-radius: 20px;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.filter-button:hover {
  background-color: var(--gray-200);
}
.filter-button__color {
  width: 21px;
  height: 21px;
  border-radius: 50%;
}
.filter-button.green .filter-button__color {
  background-color: var(--green);
}
.filter-button.blue .filter-button__color {
  background-color: var(--blue);
}
.filter-button.active {
  outline: 1px solid var(--green);
}
.filter-button__text {
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0;
}

.result-card {
  position: relative;
  bottom: 40px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 13px;
  align-items: center;
  padding: 10px 30px 35px;
  background-color: var(--back-primary);
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.25);
}
.result-card:hover .result-card__close {
  opacity: 1;
}
.result-card__close {
  position: absolute;
